.consultation {
  display: flex;
  justify-content: space-between;
  padding: 70px 120px 152px;

  &__text {
    font-size: 16px;
    line-height: 27px;
    color: #3e3e3e;
    max-width: 412px;
  }

  &__heading {
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    color: #0075ff;
  }

  &__form {
    box-sizing: border-box;
    width: 394px;
  }

  &__button {
    width: 413px;
  }
}
